
@import "../node_modules/bootstrap/scss/bootstrap";
@tailwind base;
@tailwind components;
@tailwind utilities;

.form-select {
  border-color: theme('colors.primary.600');
  border-radius: 4.5px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

body * {
  font-family: Verdana, Geneva, sans-serif;
}

body {
  background-color: whitesmoke;
}


input:not([type='checkbox']):not([type='submit']):not([type='radio']).input, textarea.input {
  @apply tw-shadow tw-appearance-none tw-border tw-border-solid tw-border-primary-600 tw-rounded tw-py-2 tw-px-3 tw-text-gray-700 tw-leading-tight focus:tw-outline-none my-1;
  width: 100%; //because it must not be !important

  &:disabled, .disabled {
    @apply tw-cursor-not-allowed tw-opacity-[65%];
  }

  &.secondary {
    @apply tw-border-secondary-600;
  } 
  &.success {
    @apply tw-border-success-600;
  }
  &.warn {
    @apply tw-border-warn-500;
  }

  &.error {
    @apply tw-border-danger-600;
  }

}

div.loader {
  @apply tw-block tw-w-[50px] tw-h-[50px] tw-rounded-full tw-border-8 tw-border-solid tw-border-primary-300 tw-border-r-primary-700;

  animation: loading 1s infinite linear;

  &.secondary {
      @apply tw-border-secondary-300 tw-border-r-secondary-700;
    }
  &.success {
    @apply tw-border-success-200 tw-border-r-success-600;
  }
  &.warn {
    @apply tw-border-warn-300 tw-border-r-warn-700;
  }
  &.danger {
    @apply tw-border-danger-200 tw-border-r-danger-600;
  }

}

@keyframes loading {
  to {
    transform: rotate(1turn)
  }
}